* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.app {
    font-family: Helvetica, sans-serif;
}

.app .container {
    display: flex;
    flex-flow: row nowrap;
}


.app .container section {
    display: flex;
    flex-direction: column;

    grid-gap: 30px;

    gap: 30px;

    width: 33.3%;
    border: 1px solid transparent;

    padding: 10px;
}

.app .container section>* {
    width: 100%;
}



.post-form {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.post-form form {
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
    padding: 20px;
}

form input {
    padding: 5px;
}

form input:focus {
    outline: none;
    border-color: transparent;
    background-color: #bec7d7;
}

form input[type='submit'] {
    color: white;
    background-color: #4267B2;
    cursor: pointer;
}
.header {
    
    /* position: fixed; */
    /* top: 0; */

    display: flex;
    justify-content: flex-start;
    align-items: center;

    padding: 0 20px;

    width: 100%;
    height: 50px;

    color: white;
    background-color: #4267B2;

    z-index: 5;
}

.header .brand {
    font-size: 20px;
    font-weight: 600;
}
.poll {
    width: 100%;
    font-size: .9rem;
}

.poll .question {
    color: white;
    background-color: #4267B2;
    padding: 10px;
}
.poll .option-container {
    display: flex;
    flex-flow: column nowrap;
    grid-gap: 5px;
    gap: 5px;
    padding: 10px;
}

.option {
    display: flex;
    flex-flow: row nowrap;

    width: 100%;

    cursor: pointer;
    background-color: rgb(223, 223, 223);
    /* border: 2px solid #a8c6ff; */
}

.option:hover {
    background-color: #a8c6ff;
}
.option:active {
    color: white;
    background-color: #4267B2;
}
.option > * {
    width: 100%;
    padding: 5px;
}


.option .option-title {}
.option .option-percentage {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.title {
    color: white;
    background-color: #4267B2;

    font-size: .8rem;
    text-align: center;

    padding: 3px;
}
.poll-list {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.poll-list .container {
    display: flex;
    flex-flow: column nowrap;
    padding: 10px;
}

.poll-list p {
    text-align: center;
    width: 100%;
    font-size: .7rem;
}
.user-section {

}


.user-card {
    padding: 50px;
}

.user-card .container {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
}

.user-card .container .user-name {}

.user-card .container .user-contact {
    color: grey;
    font-size: .8rem;
}

.user-image {
    width: 150px;
    object-fit: cover;
}
